"use client";
//React and Next
import { useState, useEffect } from "react";

//API
import {
  getsellerDatalist,
  getsellerDatalistcount,
} from "@/api/clientsideaction/getfilter";

//3rd Party
import { useRouter } from "nextjs-toploader/app";
//Redux
import { useDispatch, useSelector } from "react-redux";
import { offsetCount } from "@/redux/slices/userSlice";

//Components
import FilterSeller from "./Filter";
import SellerList from "./SellerList";
import Breadcrumb from "@/Components/Breadcrumbs";
import { useTranslation } from "react-i18next";

export default function SellerProduct({
  token,
  category,
  sellerdata,
  sellercount,
}) {
  const dispatch = useDispatch();
  const [search, setSearch] = useState("");
  const [selectedval, setSelectedval] = useState([]);
  const [attmanuIdArr, setattmanuIdArr] = useState([]);
  const [loader, setLoader] = useState(false);
  const [loaders, setLoaders] = useState(false);
  const [sellerList, setSellerList] = useState();
  const [headfil, setHeadfil] = useState([]);
  const [fildata, setFildata] = useState([]);
  const [slide, setSlide] = useState([]);
  const [trigger, setTrigger] = useState(0);
  const { t } = useTranslation();

  // Pagination
  let router = useRouter();
  const offset = useSelector((state) => state.userReducer.offsetVal);
  // const off = searchparams.get('offset') ? searchparams.get('offset') : 0
  // const cleanedStr = off && off.replace(/[a-zA-Z]+/g, '') ? off.replace(/[a-zA-Z]+/g, '') : 0;
  // const [offset, setOffset] = useState(parseInt(cleanedStr))
  const [currentPage, setCurrentPage] = useState(1);
  const [totalCount, setTotalCount] = useState(sellercount?.data);

  useEffect(() => {
    if (category) {
      let array = [];
      category?.map((filters) => {
        let obj = [];
        filters?.children?.map((item, index) => {
          obj.push(item.categoryId);
        });
        array.push(obj);
      });
      setFildata(array);
      setLoaders(true);
    }
    if (sellerdata) {
      const sellerlist = [...sellerdata?.data];
      const currentSlide = [...slide];
      sellerlist?.map((seller, index) => {
        if (seller?.vendorMedia?.length > 0) {
          const media = seller?.vendorMedia?.filter(
            (media) => media?.mediaType == 1
          );
          seller.vendorMedia = media;
          const defaultImageIndex = media?.findIndex(
            (media) => media?.defaultImage == 1
          );
          currentSlide[index] = defaultImageIndex;
        }
        if (seller?.capabilities?.length > 0) {
          const capabilitie = seller?.capabilities?.filter(
            (cap) => cap.data !== ""
          );
          seller.capabilities = capabilitie;
        }
      });
      setSlide(currentSlide);
      setSellerList(sellerlist);
      setLoader(true);
    }
  }, []);

  useEffect(() => {
    getsellerDatalist(attmanuIdArr, setSellerList, setLoader, offset);
  }, [trigger]);

  useEffect(() => {
    let array = [];
    for (let index = 0; index < fildata?.length; index++) {
      if (fildata?.[index].some((id) => selectedval?.includes(id))) {
        array.push(true);
      } else {
        array.push(false);
      }
      setHeadfil(array);
    }
  }, [selectedval]);

  const handleRemove = (id, name) => {
    setLoader(false);
    if (selectedval?.indexOf(id) !== -1) {
      setSelectedval(selectedval?.filter((ind) => ind != id));
    } else {
      setSelectedval([...selectedval, id]);
    }

    let manuSubArr = attmanuIdArr;
    if (manuSubArr.indexOf(name) != -1) {
      manuSubArr = manuSubArr.filter((manufactId) => manufactId != name);
    } else {
      manuSubArr.push(name);
    }
    setattmanuIdArr(manuSubArr);
    // setOffset(0)
    dispatch(offsetCount(0));
    getsellerDatalist(manuSubArr, setSellerList, setLoader, 0);
    getsellerDatalistcount(manuSubArr, setTotalCount);
    // router.push(`/seller-product?offset=0`)
    setCurrentPage(1);
  };

  const handleReset = () => {
    setLoader(false);
    setSelectedval([]);
    setattmanuIdArr([]);
    // setOffset(0)
    dispatch(offsetCount(0));
    getsellerDatalist("", setSellerList, setLoader, 0);
    setTotalCount(sellercount?.data);
    // router.push(`/seller-product?offset=0`)
    setCurrentPage(1);
  };

  const handlechecked = (e, itemId, id) => {
    window.scrollTo({
      top: 0,
      behavior: "smooth", // smooth scrolling animation
    });
    setLoader(false);
    let manuSubArr = attmanuIdArr;
    if (manuSubArr.indexOf(itemId) !== -1) {
      manuSubArr = manuSubArr.filter((manufactId) => manufactId != itemId);
    } else {
      manuSubArr.push(itemId);
    }
    setattmanuIdArr(manuSubArr);
    // setOffset(0)
    dispatch(offsetCount(0));
    getsellerDatalist(manuSubArr, setSellerList, setLoader, 0);
    getsellerDatalistcount(manuSubArr, setTotalCount);
    // router.push(`/seller-product?offset=0`)
    setCurrentPage(1);

    if (selectedval?.indexOf(id) != -1) {
      setSelectedval(selectedval?.filter((ind) => ind != id));
    } else {
      setSelectedval([...selectedval, id]);
    }
    const array = selectedval;
    if (selectedval?.indexOf(id) != -1) {
      setSelectedval(selectedval?.filter((ind) => ind != id));
    } else {
      setSelectedval([...selectedval, id]);
    }
  };

  const handleSearch = (e, section) => {
    setSearch(e.target.value);
    const datasort = section.sort((a, b) => {
      if (a.name.toLowerCase().includes(e.target.value.toLowerCase()))
        return -1;
      if (b.name.toLowerCase().includes(e.target.value.toLowerCase())) return 1;
      return 0;
    });
  };
  const breadCrumb = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.sellerList"),
      url: "javascript:void(0)",
    },
  ];

  // Pagination
  const onPageChange = (data) => {
    window.scrollTo({
      top: 0,
      behavior: "smooth", // smooth scrolling animation
    });
    let limit = 10;
    let offset = Math.ceil((data - 1) * limit);
    // setOffset(offset)
    dispatch(offsetCount(offset));
    setCurrentPage(data);
    // router.push(`/seller-product?offset=${offset}`)
    setTrigger(trigger + 1);
    setLoader(false);
  };
  const nPages1 = Math.ceil(totalCount != undefined && totalCount / 10);
  useEffect(() => {
    setCurrentPage(offset / 10 + 1);
  }, []);

  return (
    <>
      <div className="m-auto max-w-screen-2xl">
        <Breadcrumb breadCrumb={breadCrumb} />
      </div>
      <div className="flex flex-row mx-auto mt-4 px-4 lg:px-4 pb-6 max-w-screen-2xl">
        <FilterSeller
          category={category}
          handleReset={handleReset}
          handleSearch={handleSearch}
          selectedval={selectedval}
          handlechecked={handlechecked}
          loaders={loaders}
        />
        <SellerList
          token={token}
          sellerdata={sellerList}
          loader={loader}
          handleRemove={handleRemove}
          headfil={headfil}
          category={category}
          selectedval={selectedval}
          slide={slide}
          setSlide={setSlide}
          //Pagination
          pagecount={totalCount}
          nPages1={nPages1}
          currentPage={currentPage}
          onPageChange={onPageChange}
        />
      </div>
    </>
  );
}
