//React and Next
import Link from "next/link";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useRouter } from "nextjs-toploader/app";
//API
import { ContactSellerApi } from "@/api/clientsideaction/buyerapi";
//Utils
import { imageUrl } from "@/utilities/url";
import { GetCustomerProfile } from "@/utilities/localStorage";
//Components
import ContactSeller from "../../ContactSeller";
import ToastMessage from "@/Components/ToastMessage";
import PaginationComp from "@/Components/Pagination";
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";

export default function SellerList({
  token,
  sellerdata,
  loader,
  handleRemove,
  headfil,
  category,
  selectedval,
  slide,
  setSlide,
  //Pagination
  pagecount,
  nPages1,
  currentPage,
  onPageChange,
}) {
  const { t } = useTranslation();
  const [currentSlides, setCurrentSlides] = useState(1);
  const [isActive, setActive] = useState(false);
  const [contactSeller, setContactSeller] = useState(false);
  const [sellerEmail, setSellerEmail] = useState("");
  const [contactSellerDescripition, setContactSellerDescripition] =
    useState("");
  const [contactSellerDescripitionError, setContactSellerDescripitionError] =
    useState("");
  const [contactSellerSubmit, setContactSellerSubmit] = useState(false);
  const [attachmentFile, setAttachmentFile] = useState([]);
  const [sellerCompanyName, setSellerCompanyName] = useState("");
  const [sellerName, setSellerName] = useState("");
  const customerProfile = GetCustomerProfile();
    const router = useRouter();

  const handleContactSeller = (seller) => {
    if (token) {
      setContactSeller(true);
      setSellerEmail(seller?.customer?.email);
      setSellerName(seller?.customer?.firstName + seller?.customer?.lastName);
      setSellerCompanyName(seller?.companyName);
    } else {
      router.push("/buyer-login");
    }
  };
  useEffect(() => {
    if (contactSeller == true) {
      document.body.classList.add("overflow-hidden");
    } else {
      document.body.classList.remove("overflow-hidden");
    }
  });

  /// seller images Next slide
  const handleNextSlides = (index, length) => {
    let newSlide = currentSlides === length - 1 ? 0 : currentSlides + 1;
    setCurrentSlides(newSlide);
    setActive(!isActive);
    const newShowFullDescriptions = [...slide];
    newShowFullDescriptions[index] = newSlide;
    setSlide(newShowFullDescriptions);
  };

  /// seller images Previous slide
  const handlePrevSlides = (index, length) => {
    let newSlide = currentSlides === 0 ? length - 1 : currentSlides - 1;
    setCurrentSlides(newSlide);
    setActive(!isActive);
    const newShowFullDescriptions = [...slide];
    newShowFullDescriptions[index] = newSlide;
    setSlide(newShowFullDescriptions);
  };

  const handleContactSellerChange = (e) => {
    setContactSellerDescripition(e.target.value.trimStart());
  };

  const handleAddAttachment = (event) => {
    let addFile = [...attachmentFile];
    const file = event.target.files[0];
    if (file.name?.match(/\.(pdf)$/)) {
      const reader = new FileReader();
      reader.onloadend = () => {
        let obj = {
          file: reader.result,
          fileName: file.name,
        };
        addFile.push(obj);
        setAttachmentFile(JSON.parse(JSON.stringify(addFile)));
      };
      reader.readAsDataURL(file);
    } else {
      ToastMessage({
        type: "error",
        message: t("toastMessage.addAttachmentFile"),
      });
    }
  };
  const handleRemoveFile = (index) => {
    attachmentFile.splice(index, 1);
    setAttachmentFile([...attachmentFile]);
  };

  const contactSellerValidation = () => {
    const obj = {
      description: true,
    };
    if (contactSellerDescripition !== "") {
      setContactSellerDescripitionError("");
      obj.description = true;
    } else {
      setContactSellerDescripitionError(
        t("validationMessage.descriptionEmpty")
      );
      obj.description = false;
    }
    return obj.description;
  };

  useEffect(() => {
    if (contactSellerSubmit) {
      contactSellerValidation();
    }
  }, [contactSellerDescripition]);
  const closeContactSeller = () => {
    setAttachmentFile([]);
    setContactSellerDescripition("");
    setContactSellerSubmit(false);
    setContactSeller(false);
  };

  const handleSubmitContactSeller = async () => {
    setContactSellerSubmit(true);
    if (contactSellerValidation()) {
      setContactSellerDescripitionError("");
      const obj = {
        firstName: customerProfile.firstName,
        lastName: customerProfile.lastName,
        emailId: customerProfile.email,
        files: attachmentFile,
        userRequirements: contactSellerDescripition,
        vendorEmailId: sellerEmail,
      };
      ContactSellerApi(obj, closeContactSeller);
    }
  };

  return (
    <>
      <div className="w-full px-0 overflow-hidden sm:px-4">
        <div className="mb-6 pb-2.5 border-grey border-b">
          <p className="text-lg font-black text-black-300">
            {t("sellerlist.Results")}{" "}
            <span className="text-sm font-normal text-grey-400">
              ({pagecount} {t("seller.sellers")})
            </span>
          </p>
        </div>
        <div className="flex flex-wrap gap-4 gap-y-1">
          {category &&
            category?.map(
              (data, ind) =>
                headfil?.[ind] == true && (
                  <div key={ind} className="flex items-center mb-4 space-x-2">
                    <h3 className="text-xs font-normal text-black-300 whitespace-nowrap">
                      {data?.name} :
                    </h3>
                    <div className="flex flex-wrap items-center gap-2">
                      {data &&
                        data?.children &&
                        data?.children?.map(
                          (item, index) =>
                            selectedval &&
                            selectedval?.includes(item?.categoryId) && (
                              <div
                                key={index}
                                className="flex items-center px-2 py-1 space-x-2 rounded bg-grey-300"
                              >
                                <p className="text-xs font-normal text-darkblack">
                                  {item?.name}
                                </p>
                                <a
                                  role="button"
                                  className="cursor-pointer"
                                  onClick={(e) =>
                                    handleRemove(
                                      item?.categoryId,
                                      item?.categorySlug
                                    )
                                  }
                                >
                                  <img
                                    src="/img/close-dark.svg"
                                    alt="close-dark"
                                  />
                                </a>
                              </div>
                            )
                        )}
                    </div>
                  </div>
                )
            )}
        </div>
        {loader == false && (
          <div className="w-full px-0 sm:px-4">
            {[0, 1, 2].map((_, index) => (
              <div
                key={index}
                className="p-4 mb-4 border border-grey rounded-xl"
              >
                <div className="flex flex-col items-start justify-between gap-2 mb-4 sm:flex-row sm:items-center">
                  <div className="flex items-center space-x-2 rtl:space-[unset]">
                    <div className="flex items-center justify-center w-12 h-12 bg-gray-300 rounded rtl:ml-2">
                      <svg
                        className="w-5 h-5 text-gray-200 dark:text-gray-600"
                        aria-hidden="true"
                        xmlns="http://www.w3.org/2000/svg"
                        fill="currentColor"
                        viewBox="0 0 16 20"
                      >
                        <path d="M14.066 0H7v5a2 2 0 0 1-2 2H0v11a1.97 1.97 0 0 0 1.934 2h12.132A1.97 1.97 0 0 0 16 18V2a1.97 1.97 0 0 0-1.934-2ZM10.5 6a1.5 1.5 0 1 1 0 2.999A1.5 1.5 0 0 1 10.5 6Zm2.221 10.515a1 1 0 0 1-.858.485h-8a1 1 0 0 1-.9-1.43L5.6 10.039a.978.978 0 0 1 .936-.57 1 1 0 0 1 .9.632l1.181 2.981.541-1a.945.945 0 0 1 .883-.522 1 1 0 0 1 .879.529l1.832 3.438a1 1 0 0 1-.031.988Z" />
                        <path d="M5 5V.13a2.96 2.96 0 0 0-1.293.749L.879 3.707A2.98 2.98 0 0 0 .13 5H5Z" />
                      </svg>
                    </div>
                    <div>
                      <div className="bg-gray-200 dark:bg-gray-700 mb-2 rounded-full w-48 h-2.5"></div>
                      <div className="w-20 h-2 bg-gray-200 rounded-full dark:bg-gray-700"></div>
                    </div>
                  </div>
                  <div className="flex items-center gap-[5px] w-full sm:w-auto whitespace-nowrap">
                    <div className="w-24 h-8 bg-gray-300 rounded dark:bg-gray-700"></div>
                  </div>
                </div>
                <div className="flex flex-col items-start justify-between lg:flex-row">
                  <div>
                    <div className="w-20 h-2 bg-gray-200 rounded-full dark:bg-gray-700"></div>
                  </div>
                  <div className="flex flex-col items-center w-full gap-6 lg:flex-row md:w-auto">
                    <div className="flex items-start space-x-4">
                      <div className="flex flex-col group hover:shadow-lg rtl:ml-4">
                        <div className="flex justify-center items-center bg-gray-300 w-full h-full sm:h-[152px] overflow-hidden">
                          <svg
                            className="w-5 h-5 text-gray-200 dark:text-gray-600"
                            aria-hidden="true"
                            xmlns="http://www.w3.org/2000/svg"
                            fill="currentColor"
                            viewBox="0 0 16 20"
                          >
                            <path d="M14.066 0H7v5a2 2 0 0 1-2 2H0v11a1.97 1.97 0 0 0 1.934 2h12.132A1.97 1.97 0 0 0 16 18V2a1.97 1.97 0 0 0-1.934-2ZM10.5 6a1.5 1.5 0 1 1 0 2.999A1.5 1.5 0 0 1 10.5 6Zm2.221 10.515a1 1 0 0 1-.858.485h-8a1 1 0 0 1-.9-1.43L5.6 10.039a.978.978 0 0 1 .936-.57 1 1 0 0 1 .9.632l1.181 2.981.541-1a.945.945 0 0 1 .883-.522 1 1 0 0 1 .879.529l1.832 3.438a1 1 0 0 1-.031.988Z" />
                            <path d="M5 5V.13a2.96 2.96 0 0 0-1.293.749L.879 3.707A2.98 2.98 0 0 0 .13 5H5Z" />
                          </svg>
                        </div>
                        <div className="p-2">
                          <div className="bg-gray-200 dark:bg-gray-700 mb-2 rounded-full w-48 h-2.5"></div>
                          <div className="w-20 h-2 bg-gray-200 rounded-full dark:bg-gray-700"></div>
                        </div>
                      </div>
                      <div className="flex flex-col group hover:shadow-lg">
                        <div className="flex justify-center items-center bg-gray-300 w-full h-full sm:h-[152px] overflow-hidden">
                          <svg
                            className="w-5 h-5 text-gray-200 dark:text-gray-600"
                            aria-hidden="true"
                            xmlns="http://www.w3.org/2000/svg"
                            fill="currentColor"
                            viewBox="0 0 16 20"
                          >
                            <path d="M14.066 0H7v5a2 2 0 0 1-2 2H0v11a1.97 1.97 0 0 0 1.934 2h12.132A1.97 1.97 0 0 0 16 18V2a1.97 1.97 0 0 0-1.934-2ZM10.5 6a1.5 1.5 0 1 1 0 2.999A1.5 1.5 0 0 1 10.5 6Zm2.221 10.515a1 1 0 0 1-.858.485h-8a1 1 0 0 1-.9-1.43L5.6 10.039a.978.978 0 0 1 .936-.57 1 1 0 0 1 .9.632l1.181 2.981.541-1a.945.945 0 0 1 .883-.522 1 1 0 0 1 .879.529l1.832 3.438a1 1 0 0 1-.031.988Z" />
                            <path d="M5 5V.13a2.96 2.96 0 0 0-1.293.749L.879 3.707A2.98 2.98 0 0 0 .13 5H5Z" />
                          </svg>
                        </div>
                        <div className="p-2">
                          <div className="bg-gray-200 dark:bg-gray-700 mb-2 rounded-full w-48 h-2.5"></div>
                          <div className="w-20 h-2 bg-gray-200 rounded-full dark:bg-gray-700"></div>
                        </div>
                      </div>
                      <div className="flex justify-center items-center bg-gray-300 w-44 h-full sm:h-[152px] overflow-hidden">
                        <svg
                          className="w-5 h-5 text-gray-200 dark:text-gray-600"
                          aria-hidden="true"
                          xmlns="http://www.w3.org/2000/svg"
                          fill="currentColor"
                          viewBox="0 0 16 20"
                        >
                          <path d="M14.066 0H7v5a2 2 0 0 1-2 2H0v11a1.97 1.97 0 0 0 1.934 2h12.132A1.97 1.97 0 0 0 16 18V2a1.97 1.97 0 0 0-1.934-2ZM10.5 6a1.5 1.5 0 1 1 0 2.999A1.5 1.5 0 0 1 10.5 6Zm2.221 10.515a1 1 0 0 1-.858.485h-8a1 1 0 0 1-.9-1.43L5.6 10.039a.978.978 0 0 1 .936-.57 1 1 0 0 1 .9.632l1.181 2.981.541-1a.945.945 0 0 1 .883-.522 1 1 0 0 1 .879.529l1.832 3.438a1 1 0 0 1-.031.988Z" />
                          <path d="M5 5V.13a2.96 2.96 0 0 0-1.293.749L.879 3.707A2.98 2.98 0 0 0 .13 5H5Z" />
                        </svg>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
            <span className="sr-only">{t("productcontent.loading")}</span>
          </div>
        )}
        {loader == true && sellerdata && sellerdata?.length != 0
          ? sellerdata?.map((data, index) => (
              <div
                key={index}
                className="p-4 mb-4 border border-grey rounded-xl"
              >
                <div className="flex flex-col items-start justify-between gap-2 mb-4 sm:flex-row sm:items-center">
                  <div className="flex items-center space-x-2 rtl:space-x-[unset]">
                    <div className="flex items-center w-12 h-12 rtl:ml-2">
                      <img
                        src={
                          imageUrl +
                          "?path=" +
                          data?.companyLogoPath +
                          "&name=" +
                          data?.companyLogo +
                          "&width=60&height=60"
                        }
                        alt={data?.companyName}
                        onError={({ currentTarget }) => {
                          currentTarget.onerror = null;
                          currentTarget.src = "/img/user1.jpg";
                          currentTarget.alt = "user";
                        }}
                      />
                    </div>
                    <div className="flex flex-col space-y-1">
                      <Link
                        prefetch={false}
                        href={`/shop/${data?.displayNameUrl}`}
                        className="relative text-base font-black leading-5 text-black-300 animation"
                      >
                        {data?.companyName}
                      </Link>
                      <p className="text-xs font-normal text-black-300">
                        {data?.companyCountryName}
                      </p>
                    </div>
                  </div>
                  <div className="flex items-center gap-[5px] w-full sm:w-auto whitespace-nowrap">
                    <a
                      role="button"
                      className="flex justify-center items-center space-x-[5px] rtl:space-x-[unset] hover:bg-slate-100 hover:shadow-md px-4 border border-black rounded w-full h-[34px] font-semibold text-black text-sm cursor-pointer btn"
                      onClick={(e) => handleContactSeller(data)}
                    >
                      <img
                        src="/img/mail.svg"
                        alt="mail"
                        className="rtl:ml-[5px]"
                      />
                      <p>{t("orderhistory.Contactseller")}</p>
                    </a>
                  </div>
                </div>
                <div className="flex flex-col items-start justify-between lg:flex-row">
                  <div className="">
                    <h5 className="mb-2.5 font-normal text-black-300 text-sm leading-4">
                      {t("orderhistory.Caps")}
                    </h5>
                    {data?.capabilities?.length > 0 ? (
                      data?.capabilities?.map(
                        (capabilitie, i) =>
                          capabilitie?.status == 1 &&
                          i <= 4 && (
                            <div
                              key={i}
                              className="flex items-center gap-[5px] mb-2"
                            >
                              <p className="text-sm font-bold leading-4 text-black-300">
                                {capabilitie.data}
                              </p>
                            </div>
                          )
                      )
                    ) : (
                      <div className="flex items-center gap-[5px] mb-2">
                        <p className="text-sm font-bold leading-4 text-black-300">
                          {t("orderhistory.NoCaps")}
                        </p>
                      </div>
                    )}
                  </div>
                  <div className="flex lg:flex-row flex-col items-end rtl:space-x-[unset] lg:space-x-6 space-y-6 lg:space-y-0 w-full md:w-auto">
                    <div className="flex flex-col items-end space-y-3 lg:rtl:ml-6">
                      <div className="flex items-start space-x-4">
                        {data?.products?.map((sellerimg, index) => (
                          <a
                            role="button"
                            key={index}
                            className="flex flex-col cursor-pointer group hover:shadow-lg"
                          >
                            <div className="flex items-center w-full sm:w-[152px] h-full sm:h-[152px] overflow-hidden">
                              <img
                                src={
                                  imageUrl +
                                  "?path=" +
                                  sellerimg?.containerName +
                                  "&name=" +
                                  sellerimg?.image +
                                  "&width=250&height=250"
                                }
                                alt={sellerimg?.name}
                                onError={({ currentTarget }) => {
                                  currentTarget.onerror = null;
                                  currentTarget.src = "/img/no-image.png";
                                  currentTarget.alt = "no-image";
                                }}
                                className="group-hover:scale-105"
                              />
                            </div>
                            <div className="p-2">
                              <h6 className="mb-[3px] font-bold text-black-300 text-sm leading-4">
                                <CustomCurrencyFormatter
                                  price={sellerimg?.price}
                                />
                              </h6>
                              <p className="font-normal text-[10px] text-black-300 leading-3">
                                {t("seller.MOQ")}{" "}
                                {sellerimg?.maxQuantityAllowedCart}{" "}
                                {t("seller.pieces")}
                              </p>
                            </div>
                          </a>
                        ))}
                      </div>
                      <Link
                        prefetch={false}
                        href={`/shop/${data?.displayNameUrl}`}
                        className="relative text-sm font-normal leading-5 text-link animation"
                      >
                        {t("orderhistory.viewmore")}..
                      </Link>
                    </div>
                    {data.vendorMedia?.length > 0 && (
                      <div
                        id="controls-carousel"
                        className="relative w-full sm:w-72"
                        data-carousel="static"
                      >
                        <div className="relative h-[14.5rem] overflow-hidden">
                          {data.vendorMedia?.map((img, imgIndex) => (
                            <div
                              key={imgIndex}
                              className={` duration-700 ease-in-out  absolute inset-0 transition-transform transform   ${
                                slide[index] == imgIndex
                                  ? "translate-x-0 z-30"
                                  : slide[index] < imgIndex
                                  ? "translate-x-full"
                                  : slide[index] != imgIndex &&
                                    "-translate-x-full"
                              } `}
                              data-carousel-item
                            >
                              <img
                                src={
                                  imageUrl +
                                  "?path=" +
                                  img?.filePath +
                                  "&name=" +
                                  img?.fileName +
                                  "&width=200&height=200"
                                }
                                alt={img?.title}
                                className={`absolute block w-full -translate-x-1/2 object-cover -translate-y-1/2 top-1/2 left-1/2 h-full `}
                              />
                            </div>
                          ))}

                          <span className="bottom-4 left-[40%] z-50 absolute flex items-center space-x-1 rtl:space-x-[unset] bg-black hover:bg-zinc-800 px-4 py-1.5 rounded-xl h-6 font-normal text-white text-xs cursor-pointer btn">
                            <img
                              src="/img/seller-list-photo.svg"
                              alt="seller-list-photo"
                              className="rtl:ml-1"
                            />
                            <p>{data.vendorMedia?.length}</p>
                          </span>
                        </div>

                        <button
                          onClick={(e) =>
                            handlePrevSlides(index, data.vendorMedia?.length)
                          }
                          className="left-0 z-40 absolute inset-y-1/2 bg-trans-black opacity-20 hover:opacity-100 m-auto px-[9px] h-full text-gray-400 text-5xl transition-opacity cursor-pointer"
                        >
                          <img
                            src="/img/right-carosel.svg"
                            alt="right-carosel"
                          />
                        </button>
                        <button
                          onClick={(e) =>
                            handleNextSlides(index, data.vendorMedia?.length)
                          }
                          className="right-0 z-40 absolute inset-y-1/2 bg-trans-black opacity-20 hover:opacity-100 m-auto px-[9px] h-full text-gray-400 text-5xl transition-opacity cursor-pointer"
                        >
                          <img
                            src="/img/right-carosel.svg"
                            alt="right-carosel"
                            className="rotate-180"
                          />
                        </button>
                      </div>
                    )}
                  </div>
                </div>
              </div>
            ))
          : loader == true && (
              <div className="mx-auto mt-32 min-h-[40vh]">
                <div className="flex flex-col items-center justify-center gap-8 p-4 text-center sm:flex-row">
                  <img src="/img/no-data.svg" alt="no-data" />
                  <div>
                    <h5 className="mb-1 text-xl font-black text-black-300">
                      {t("myprofile.NoResults")}{" "}
                    </h5>
                  </div>
                </div>
              </div>
            )}
        <div className="relative flex flex-col w-full mt-4 sm:flex-row">
          {pagecount >= 10 && (
            <>
              <div className="absolute left-0 hidden sm:block top-4">
                {t("productlist.pages")} {currentPage} {t("productlist.of")}{" "}
                {nPages1}
              </div>

              <PaginationComp
                nPages1={nPages1}
                currentPage={currentPage}
                onPageChange={onPageChange}
              />
            </>
          )}
        </div>
      </div>

      <ContactSeller
        contactSeller={contactSeller}
        setContactSeller={setContactSeller}
        sellerEmail={sellerName}
        handleContactSellerChange={handleContactSellerChange}
        handleAddAttachment={handleAddAttachment}
        attachmentFile={attachmentFile}
        handleRemoveFile={handleRemoveFile}
        customerProfile={customerProfile}
        handleSubmitContactSeller={handleSubmitContactSeller}
        contactSellerDescripition={contactSellerDescripition}
        contactSellerDescripitionError={contactSellerDescripitionError}
        closeContactSeller={closeContactSeller}
        sellerCompanyName={sellerCompanyName}
      />
    </>
  );
}
